.form-send-tweet{
    background-color: #fff;
    border: 2px solid #00acee;
    padding: 20px;
    width: 90%;
    color: black;
    

    &__title{
        margin: 0 !important;
    }

    &__form{
        .MuiFormControl-root{
            width: 100%;
        }

        &-name{
            width: 70% !important;
        }

        &-textarea{
            width: 100%;
        }
    }
}